'use-strict';
//sessionStorage.clear();
//localStorage.clear();
let listaRestaurantes = document.getElementById('listaRestaurantes');
let contedorLista = document.getElementById('contedorLista');
let seleccionado = false;
let counter = (localStorage.getItem('counter') ? parseInt(localStorage.getItem('counter')) : 0)
const arrayDeMarcadores = []

if ("geolocation" in navigator) {
    navigator.geolocation.getCurrentPosition(function (position) {
        //const { latitude, longitude } = position.coords;

        const mapa = L.map("mapa").setView([42.78493742707595, -7.727758789062501], 8);

        L.tileLayer(
            "https://server.arcgisonline.com/ArcGIS/rest/services/World_Imagery/MapServer/tile/{z}/{y}/{x}",
            {
                attribution:
                    "Tiles &copy; Esri &mdash; Source: Esri, i-cubed, USDA, USGS, AEX, GeoEye, Getmapping, Aerogrid, IGN, IGP, UPR-EGP, and the GIS User Community",
            }
        ).addTo(mapa);

        //// MANTER OS MARCADORES ////
        function manterMarcadores() {
            for (chave of Object.keys(localStorage).sort()) {
                if (
                    chave !== "IsThisFirstTime_Log_From_LiveServer" &&
                    chave !== "counter"
                ) {

                    if (localStorage.getItem(chave) !== '') {
                        const { latitude, longitude, desc } = JSON.parse(
                            localStorage.getItem(chave)
                        );

                        let newListMarquer = document.createElement("li");
                        newListMarquer.id = chave;
                        newListMarquer.innerHTML = desc;
                        newListMarquer.style.cursor = 'pointer';
                        newListMarquer.style.backgroundImage =
                            "linear-gradient(to right, lightblue, white, lightblue)";
                        listaRestaurantes.prepend(newListMarquer);

                        let deleteBtn = document.createElement("button");
                        deleteBtn.innerHTML =
                            '<svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" class="bi bi-trash-fill" viewBox="0 0 16 16"><path d="M2.5 1a1 1 0 0 0-1 1v1a1 1 0 0 0 1 1H3v9a2 2 0 0 0 2 2h6a2 2 0 0 0 2-2V4h.5a1 1 0 0 0 1-1V2a1 1 0 0 0-1-1H10a1 1 0 0 0-1-1H7a1 1 0 0 0-1 1H2.5zm3 4a.5.5 0 0 1 .5.5v7a.5.5 0 0 1-1 0v-7a.5.5 0 0 1 .5-.5zM8 5a.5.5 0 0 1 .5.5v7a.5.5 0 0 1-1 0v-7A.5.5 0 0 1 8 5zm3 .5v7a.5.5 0 0 1-1 0v-7a.5.5 0 0 1 1 0z"/></svg>';
                        deleteBtn.classList.add("btn", "btn-danger", "ms-5");
                        deleteBtn.addEventListener("click", eliminarMarcador);
                        newListMarquer.append(deleteBtn);


                        getTemperatura(latitude, longitude).then(
                            function (response) {
                                let weatherDiv = document.createElement('div');
                                weatherDiv.classList.add('row', 'text-center');
                                let [temperatura, tempo] = response;
                                let tempoElement = document.createElement('p');
                                tempoElement.textContent = `${tempo.split('T')[0]}  ${tempo.split('T')[1]} h`;
                                tempoElement.classList.add('col-6', 'h5');
                                let temperaturaElement = document.createElement('p');
                                temperaturaElement.textContent = `${temperatura} ºC`;
                                temperaturaElement.classList.add('col-6', 'h5');
                                
                                weatherDiv.append(tempoElement);
                                weatherDiv.append(temperaturaElement);
                                newListMarquer.append(weatherDiv);
                            }
                        );


                        const marcador = L.marker([latitude, longitude]);
                        marcador.addTo(mapa);
                        marcador.bindPopup(desc).openPopup();
                        arrayDeMarcadores.push(marcador);
                    } else {
                        arrayDeMarcadores.push(null);
                    }
                }
            }
        }

        manterMarcadores();
        //// MANTER OS MARCADORES ////



        //// ENADIR MARCADORES AO CLICAR NO MAPA ////
        mapa.on("click", engadirMarcador);

        function engadirMarcador(e) {
            let latitude = e.latlng.lat;
            let longitude = e.latlng.lng;

            if (!seleccionado) {
                let newForm = document.createElement("form");
                newForm.action = "";
                newForm.method = "POST";
                newForm.addEventListener("submit", gardarMarador);

                let inputNome = document.createElement("input");
                inputNome.type = "text";
                inputNome.placeholder = "Nombre";
                inputNome.classList.add(["col-5", "form-control"]);

                let inputGenero = document.createElement("input");
                inputGenero.type = "text";
                inputGenero.placeholder = "Género";
                inputGenero.classList.add(["col-5", "form-control"]);

                let btnSubmit = document.createElement("button");
                btnSubmit.type = "submit";
                btnSubmit.hidden = true;

                newForm.append(inputNome);
                newForm.append(inputGenero);
                newForm.append(btnSubmit);
                
                contedorLista.append(newForm);
                seleccionado = true;

                inputNome.focus();

                counter++;
                localStorage.setItem("counter", counter);
                localStorage.setItem(
                    `marcador${counter}`,
                    JSON.stringify({ latitude: latitude, longitude: longitude })
                );

            }
        }
        //// ENADIR MARCADORES AO CLICAR NO MAPA ////



        //// ENGADIR OS MARCADORES A LISTA DE MARCADORES ////
        function gardarMarador(e) {
            e.preventDefault();

            let newListMarquer = document.createElement("li");
            newListMarquer.id = `marcador${counter}`;
            newListMarquer.innerHTML = `<b>Nombre: </b>${e.currentTarget.firstElementChild.value} - <b>Género: </b>${e.currentTarget.children[1].value}`;
            newListMarquer.style.backgroundImage =
                "linear-gradient(to right, lightblue, white, lightblue)";
            newListMarquer.style.cursor = 'pointer';
            listaRestaurantes.prepend(newListMarquer);

            let deleteBtn = document.createElement('button');
            deleteBtn.innerHTML = '<svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" class="bi bi-trash-fill" viewBox="0 0 16 16"><path d="M2.5 1a1 1 0 0 0-1 1v1a1 1 0 0 0 1 1H3v9a2 2 0 0 0 2 2h6a2 2 0 0 0 2-2V4h.5a1 1 0 0 0 1-1V2a1 1 0 0 0-1-1H10a1 1 0 0 0-1-1H7a1 1 0 0 0-1 1H2.5zm3 4a.5.5 0 0 1 .5.5v7a.5.5 0 0 1-1 0v-7a.5.5 0 0 1 .5-.5zM8 5a.5.5 0 0 1 .5.5v7a.5.5 0 0 1-1 0v-7A.5.5 0 0 1 8 5zm3 .5v7a.5.5 0 0 1-1 0v-7a.5.5 0 0 1 1 0z"/></svg>';
            deleteBtn.classList.add("btn", "btn-danger", "ms-4");
            deleteBtn.addEventListener('click', eliminarMarcador);
            newListMarquer.append(deleteBtn);

            const { latitude, longitude } = JSON.parse(
                localStorage.getItem(`marcador${counter}`)
            );



            getTemperatura(latitude, longitude).then(
                function (response) {
                    let weatherDiv = document.createElement('div');
                    weatherDiv.classList.add('row', 'text-center');
                    let [temperatura, tempo] = response;
                    let tempoElement = document.createElement('p');
                    tempoElement.textContent = `${tempo.split('T')[0]}  ${tempo.split('T')[1]} h`;
                    tempoElement.classList.add('col-6','h5');
                    let temperaturaElement = document.createElement('p');
                    temperaturaElement.textContent = `${temperatura} ºC`;
                    temperaturaElement.classList.add('col-6','h5');
                    
                    weatherDiv.append(tempoElement);
                    weatherDiv.append(temperaturaElement);
                    newListMarquer.append(weatherDiv);
                }
            );



            const marcador = L.marker([latitude, longitude]);
            marcador.addTo(mapa);
            marcador
                .bindPopup(
                    `<b>Nombre: </b>${e.currentTarget.firstElementChild.value}\n<b>Género: </b>${e.currentTarget.children[1].value}`
                )
                .openPopup();

            localStorage.setItem(
                `marcador${counter}`,
                JSON.stringify({
                    latitude: latitude,
                    longitude: longitude,
                    desc: `<b>Nombre: </b>${e.currentTarget.firstElementChild.value} - <b>Género: </b>${e.currentTarget.children[1].value}`,
                })
            );

            arrayDeMarcadores.push(marcador);

            e.currentTarget.remove();
            seleccionado = false;
        }
        //// ENGADIR OS MARCADORES A LISTA DE MARCADORES ////



        //// AO PULSAR NUN ELEMENTO NA LISTA FACER FOCUS NO MARCADOR CORRESPONDENTE ////
        listaRestaurantes.addEventListener("click", seleccionarMarcador);
        function seleccionarMarcador(e) {
            if (e.target.tagName !== 'BUTTON' && e.target.tagName !== 'svg' && e.target.tagName !== 'path') {
                let elementoSeleccionado = e.target.closest('li');
                const { latitude, longitude } = JSON.parse(localStorage.getItem(elementoSeleccionado.id));

                mapa.closePopup();
                mapa.flyTo([latitude, longitude], 17);

                /* console.log(elementoSeleccionado.id);
                console.log(parseInt(elementoSeleccionado.id.substr(elementoSeleccionado.id.length - 1)) -1);
                console.log(arrayDeMarcadores);
                console.log(arrayDeMarcadores[parseInt(elementoSeleccionado.id.substr(elementoSeleccionado.id.length - 1)) - 1]); */
                
                let marcadorSeleccionado = arrayDeMarcadores[parseInt(elementoSeleccionado.id.substr(elementoSeleccionado.id.length - 1)) -1];
                marcadorSeleccionado.openPopup();

            }
        }
        //// AO PULSAR NUN ELEMENTO NA LISTA FACER FOCUS NO MARCADOR CORRESPONDENTE ////



        //// ELIMINAR MARCADORES ////
        function eliminarMarcador(e) {
            if (e.currentTarget.tagName === 'BUTTON') {
                let elementoEliminar = e.target.closest("li");
                let marcadorElminar = arrayDeMarcadores[parseInt(elementoEliminar.id.substr(elementoEliminar.id.length - 1)) -1];

                /* console.log(elementoEliminar.id);
                console.log(parseInt(elementoEliminar.id.substr(elementoEliminar.id.length - 1)));
                console.log(arrayDeMarcadores[parseInt(elementoEliminar.id.substr(elementoEliminar.id.length - 1)) -1]); */
                console.log(arrayDeMarcadores);

                arrayDeMarcadores[parseInt(elementoEliminar.id.substr(elementoEliminar.id.length - 1)) -1] = null;
                //arrayDeMarcadores.splice(parseInt(elementoEliminar.id.substr(elementoEliminar.id.length - 1) -1), 0);
                marcadorElminar.remove();
                elementoEliminar.remove();
                
                localStorage.setItem(elementoEliminar.id, '');

                console.log(arrayDeMarcadores);
            }
        }
        //// ELIMINAR MARCADORES ////



        //// CONSULTA DE TEMPETRATURA PARA CADA MARCADOR ////
        async function getTemperatura(latitude, longitude) {
            let response = await fetch(`https://api.open-meteo.com/v1/forecast?latitude=${latitude}&longitude=${longitude}&hourly=temperature_2m&current_weather=true&timezone=Europe%2FLondon&start_date=2023-02-02&end_date=2023-02-02`);
            let temperatura;
            if (response.ok) {
                temperatura = await response.json();
                return [temperatura.current_weather.temperature, temperatura.current_weather.time];
            }
            return false;
        }
        //// CONSULTA DE TEMPETRATURA PARA CADA MARCADOR ////
    });
}